
@mixin h2() {
  width: 100%;
  max-width: 68rem;

  font-size: 6rem;
  font-family: $CormorantBold;
  line-height: 6rem;

  color: $white;
  opacity: 0.7;
  text-shadow: 0 4px 4px 0 #000;
}

@mixin h3() {
  width: 100%;
  max-width: auto;

  font-family: $CormorantBoldItalic;
  font-size: 4.8rem;
  line-height: 4.8rem;

  color: $white;
}

@mixin p ($width, $color) {
  width: 100%;
  max-width: $width;

  font-family: $OpenRegular;
  font-size: 1.6rem;
  line-height: 2.4rem;

  color: $color;
}

@mixin arrow-title () {
  display: inline-block;

  font-family: $OpenBold;
  font-size: 1.6rem;

  color: $primary_green;
  text-decoration: none;

  transition: color 0.2s linear;

  &::after {
    content: url("../images/arrow.png");
    position: relative;
    left: 8px;
    top: 3px;
  }

  &:hover {
    color: #91ac46;
  }
}

@mixin arrow-artefact() {
  display: block;

  font-family: $OpenBold;
  font-size: 1.6rem;

  color: $background_shade;
  text-decoration: none;

  transition: color 0.2s linear;

  &::after {
    content: url("../images/arrow2.png");
    position: relative;
    left: 8px;
    top: 3px;
  }

  &:hover {
    color: #688b05;
  }
}

@mixin card() {
  width: 100%;
  max-width: 25.5rem;


  border: 1px solid $background_shade;
}

@mixin cardTitle() {
  margin-bottom: 0.8rem;

  font-family: $CormorantBold;
  font-size: 2.4rem;
  line-height: 3.6rem;

  color: $white;
}

@mixin btnCard() {
  display: block;
  width: 100%;
  max-width: 14.5rem;
  margin-top: 1.8rem;
  padding: 1rem 2.8rem;

  font-family: $OpenRegular;
  font-size: 1.6rem;
  line-height: 2.4rem;

  color: $primary_green;
  outline: none;
  border: 1px solid $primary_green;
}

@mixin arrowCard() {
  width: 100%;
  max-width: 16rem;
  margin: 0 auto 8rem;

  font-family: $CormorantBold;
  font-size: 2.4rem;

  line-height: 3.6rem;
  color: $white;
}

@mixin input() {
  margin-bottom: 2.4rem;
  padding: 1.2rem 30rem 1.1rem 1.6rem;

  outline: none;
  border: none;

  &::placeholder {
    font-family: $CormorantRegular;
    font-size: 1.6rem;

    color: $grey;
  }

  &:hover {
    background-color: $grey;
    &::placeholder {
      color: $white;
    }
  }

  &:focus {
    border: solid 1px #83a440;
  }
}

@mixin btnForm() {
  width: 100%;
  max-width: 14.5rem;
  padding: 1.1rem 2.6rem;

  font-family: $OpenBold;
  font-size: 1.6rem;

  outline: none;
  background-color: $primary_green;
  color: $background_shade;
  cursor: pointer;

  &:hover {
    background-color: #91ac46;
  }

  &:focus {
    border: solid 1px #83a440;
  }
}

@mixin nav () {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  max-width: 57.6rem;

  font-family: $OpenRegular;
  font-size: 1.6rem;
  line-height: 2.4rem;

  list-style: none;

  &__item {
    margin-right: 6.4rem;

    text-decoration: none;
    color: $white;
    transition: color 0.2s linear;


    &:last-child {
      margin-right: 0;
    }

    &:hover {
      color: $primary_green;
    }
  }
}
