
.intro__text {
  padding: 11.5rem 0;

  @media (max-width: 650px) {
    padding: 7rem 0;;

    text-align: center;
  }

  @media (max-width: 500px) {
    padding: 4rem 0;;
  }

  &-title {
    @include h2();
    margin-bottom: 1.3rem;

    @media (max-width: 650px) {
      font-size: 5rem;
    }
  }

  &-info {
    @include p(63.5rem, $white);
    margin-bottom: 3.4rem;

    @media (max-width: 650px) {
      font-size: 1.4rem;
    }
  }

  &-arrow{
    @include arrow-title();
  }
}