
.header {
  margin: 1.7rem 0 1.9rem;
  display: flex;
  justify-content: space-between;

  @media (max-width: 750px) {
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: 420px) {
    margin: 1rem 0 1rem;
  }

  &__logo {
    display: block;

    cursor: pointer;

    @media (max-width: 750px) {
      margin-bottom: 2rem;
    }
  }

  &__nav {
    @include nav();

    @media (max-width: 750px) {
      margin-top: 0.8rem;

      order: 3;
    }
  }

  &__items-search {
    margin-right: 6.7rem;

    cursor: pointer;
    
    @media (max-width: 820px) {
      margin-right: 1rem;
    }
  }

  &__items-login {
    cursor: pointer;
  }
}