
.artefacts {

  &__title {
    @include h3();
    margin-bottom: 3rem;
    
    @media (max-width: 1100px) {
      text-align: center;
    }
  }

  &__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @media (max-width: 1100px) {
      flex-direction: row;
      justify-content: center;
    }
  }

  &__img {
    display: block;
    width: 100%;
    max-width: 82.5rem;
    margin-right: 3rem;

    @media (max-width: 1100px) {
      margin-bottom: 2rem;
      margin-right: 0;
    }
  }

  &__info {
    width: 100%;
    max-width: 25.5rem;
    padding: 1.6rem 1.6rem 2.4rem;

    background-color: $primary_green;

    &-title {
      margin-bottom: 0.8rem;

      font-family: $CormorantBold;
      font-size: 2.4rem;
      line-height: 3.4rem;
    }

    &-text {
      @include p(22.3rem, $green-text);
      margin-bottom: 7.5rem;
    }

    &-arrow {
      @include arrow-artefact();
      margin-bottom: 2.4rem;
    }
  }
}