
.card {
  @include card();
  height: 50rem;

  @media(max-width: 1100px) {
    margin-bottom: 2rem;
  }

  &__img {
    padding: 3rem 1rem ;

    background: url("../images/bgcard.jpg");
  }

  &__info {
    padding: 1.6rem 1.6rem 2.4rem;
  }

  &__title {
    @include cardTitle();
  }

  &__text {
    @include p(22rem, $grey);
  }

  &__button {
    @include btnCard();
    text-decoration: none;

    transition: background-color 0.2s linear, color 0.2s linear;

    position: relative;
    top: 0;
    left: 0;

    &:hover {
      color: $background_shade;
      background-color: $primary_green;
    }
  }
}
