.arrows {
  
  @media (max-width: 1100px) {
    margin-bottom: 5rem;
  }
  
  @include arrowCard();
  display: flex;
  align-items: center;

  &__count {
    margin: 0 2.5rem;
  }

  &__left {
    cursor: pointer;
  }

  &__right {
    cursor: pointer;
  }
}

