
// Open Sans

/* open-sans-400 */
@font-face {
  font-display: swap;
  font-family: 'Open-Sans-Regular';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/open_sans/open-sans-v40-cyrillic_latin-regular.woff2') format('woff2');
}
/* open-sans-600 */
@font-face {
  font-display: swap;
  font-family: 'Open-Sans-SemiBold';
  font-style: normal;
  font-weight: 600;
  src: url('../fonts/open_sans/open-sans-v40-cyrillic_latin-600.woff2') format('woff2');
}
/* open-sans-700 */
@font-face {
  font-display: swap;
  font-family: 'Open-Sans-Bold';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/open_sans/open-sans-v40-cyrillic_latin-700.woff2') format('woff2');
}

//CORMORANT

/* cormorant-regular 400 */
@font-face {
  font-display: swap;
  font-family: 'Cormorant-Regular';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/cormorant/cormorant-v21-latin-regular.woff2') format('woff2');
}
/* cormorant-700 */
@font-face {
  font-display: swap;
  font-family: 'Cormorant-Bold';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/cormorant/cormorant-v21-latin-700.woff2') format('woff2');
}
/* cormorant-700italic */
@font-face {
  font-display: swap;
  font-family: 'Cormorant-Bold-Italic';
  font-style: italic;
  font-weight: 700;
  src: url('../fonts/cormorant/cormorant-v21-latin-700italic.woff2') format('woff2');
}