
.cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 6rem 0 4.4rem;

  @media (max-width: 540px) {
    margin: 4rem 0 2.4rem;
  }

  @media(max-width: 815px) {
    justify-content: space-around;
  }

  @media(max-width: 560px) {
    justify-content: center;
  }
}
