@import './utils';
@import './components';
@import './layout';

body {
  background-color: $background;
}

.wrapper {
  width: 100%;
  max-width: 111rem;
  margin: 0 auto;

  @media(max-width: 1100px) {
    padding: 0 2.5rem;
  }
}

.intro {
  width: 100%;
  background: url("../images/intro.jpg") no-repeat center / cover;
  object-fit: cover;
}

.help {
  margin-top: 6rem;
  width: 100%;
  background: url("../images/help.jpg") no-repeat center / cover;
  object-fit: cover;
}
