
.footer {
  background-color: $background_shade;

  &__icons {
    width: 100%;
    max-width: 18.6rem;
    margin: 0 auto;
    padding: 1.8rem 0;

    &-items {
      display: flex;
      justify-content: space-between;


      &-item {
        display: block;
        margin-right: 3.4rem;

        cursor: pointer;
        text-decoration: none;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}