
.help__info {
  padding-top: 6rem;
  width: 100%;
  max-width: 82.5rem;

  @media (max-width: 780px) {
      text-align: center;
  }

  &-title {
    @include h3();
    margin-bottom: 1.6rem;
  }

  &-text {
    @include p(82.5rem, $white);
    margin-bottom: 2.4rem;
  }
}

.help__form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 35rem;
  padding-bottom: 6rem;

  @media (max-width: 780px) {
    margin: 0 auto;
  }

  &-name {
    @include input();
  }

  &-email {
    @include input();
  }
}

.btn {
  @include btnForm();

  @media (max-width: 780px) {
    margin: 0 auto;
  }
}